import { useDispatch } from 'react-redux';

import {setTransform} from 'actions/settings';

export default function Item({name, accuracy, area, onSelect, isSelected=false}) {
    const dispatch = useDispatch();
    const handleSelect = () => {
        dispatch(setTransform({name:name, accuracy:accuracy, area:area}));
        onSelect()
    };
    return (
        <div className={`result__item`} onClick={handleSelect}>
            <div className="result__item-about" >
                <h3 className='result__item-title'>Name: {name}</h3>
                <h4 className='result__item-description'>Accuracy: {accuracy}</h4>
                <h4 className='result__item-description'>Area: {area}</h4>
                {isSelected && <span style={{"color":"red"}}>Selected</span>}
            </div>
        </div > 
    );
};